
<template>
<div class="view view--char_creation" v-show='isVisible'>
        <div id="char_creation" class="wrapper">
            <div class="char_creation_container">
                <div class="char_creation_header">
                    <span class="title">
                        {{gMenuSelected[focusedMenuIndex].title}}
                    </span>                    
                    <span class="subtitle"> 
                        {{gMenuSelected[focusedMenuIndex].subTitle}}
                    </span>
                </div>
                <div class="char_creation_content">                                
                    <div class="body_char_creation" ref="scrollDiv">

                        <!-- <div class="Menu1" v-if="menuStep === 'Step1'" >   
                            <ul>
                                <li id="list" v-for="(key, value) in Menu1" v-if="key.name !== 'spacing'" :class="{ focus: value === focus }">            
                                    
                                </li>     
                            </ul>
                        </div> 
                        <div class="spacing"></div>
                        -->

                        <div :class="gMenuSelected[focusedMenuIndex].title">                
                            <ul>
                                <li id="list" v-for="(key, value) in gMenuSelected[focusedMenuIndex].inputs" :class="{ focus: value === focusedInputIndex }" v-bind:key="key" @mouseover="onHoverInput(value)"> 
                                    
                                    <div class="panel-col" v-if="key.type === 'number'" :class="{disabled: key.disabled === true}">       
                                        <div class="qtd" >                                    
                                            <span class="grey-text title">{{key.name}} </span>                             
                                        </div>
                                        <div class="buttons-item">
                                            <button class="button-left col s4 waves-effect waves-light btn" v-on:click="clickButtonLeft()">
                                                <i></i>
                                            </button>                            
                                            <span class="input-number col s4"> {{key.value}}</span>        
                                            <button class="button-right col s4 waves-effect waves-light btn" v-on:click="clickButtonRight()">
                                                <i></i>
                                            </button>               
                                        </div>                                 
                                    </div>        

                                    <!--  OTHER TYPE  -->
                                    <div class="input" v-if="key.type === 'text'" :class="{disabled: key.disabled === true}" v-on:click="onPressedEnter(value)">                                                         
                                        <input :id="[[ key.id ]]" :type="[[ key.type ]]" class="input-text" :min="[[ key.minValue ]]" :value="[[ key.value ]]"  :max="[[ key.maxValue ]]" :placeholder='[[ key.name ]]'  v-on:change="changeoptions">    
                                    </div>                             

                                    <div class="input" v-if="key.type === 'button'" :class="{disabled: key.disabled === true}" v-on:click="onPressedEnter(value)">
                                        <button class="button"><span>{{key.name}}</span></button>
                                        <div class="input-enter">  <i></i></div>
                                    </div>     
                                    
                                </li>     
                            </ul>
                        </div>
                    </div> 

                    <span class="line-divider-horizontal">{{subTitleInputSelected}}</span>       
                </div>           
            </div>
        </div>
    </div>
  
</template>

<script>

import Nui from '../utils/Nui';

export default {
    name: 'CharacterCreation',
    data() {
        return {
            isVisible: false,
            titleMenu: 'Criação de Personagem',
            maxCharacters: 2,
            gMenuSelected: 'mainMenu',
            isMale: false,
            characterName: null,
            ComponentsSaved: {
                SkinModel:1,
                SkinToneVariation: 1,
                Head: 1,
                Hair:0,
                HairColor:1,
                Mustache:0,
                MustacheColor:0,
                Overlay: { },
                pedScaleSize:180
            },   
            focusedMenuIndex: 0,
            focusedInputIndex: 0,
            subTitleInputSelected: 'Escreva o nome do seu Personagem aqui.',           
            errors: {
                charName: [],
                date: [],
            },
            Clicked: 0,   
            mouseX: 0.5,
            mouseY: 0.5,
        };
    },
    mounted(){
        window.addEventListener('keydown', this.selectMenu);

        this.listener = window.addEventListener(
        'message',
            event => {
                var data = event.data;        

                switch(data.type){
                    case "open":     
                        this.isVisible = data.isVisible;
                        this.isMale = data.isMale;       
                    break;

                    case "close":
                        this.isVisible = data.isVisible;
                    break;

                    case "receivedata":
                        
                        for (let [menuName, dataMenu] of Object.entries(data.uiMenuData)) {

                            this[menuName] = null;

                            this[menuName] = dataMenu;
                        }                    

                        var menuVariation = "mainMenu";
                        this.gMenuSelected = this[menuVariation];

                    break;
                    
                    case 'ReceiveProfileName':  
                        this.isVisible = data.isVisible;

                        var menuNameOffset = this.gMenuSelected[0].inputs;

                        menuNameOffset[0].name = data.profileName;
                        this.characterName = data.profileName;

                        menuNameOffset[1].disabled = false;                 
                        menuNameOffset[2].disabled = false;
                        menuNameOffset[3].disabled = false;
                        menuNameOffset[4].disabled = false;
                    break;

                }
            },
        false, 
        );

        document.addEventListener('mousemove', this.onMouseMove);
        document.addEventListener('mousedown', this.onMouseDown)
        document.addEventListener('wheel', this.onWheel)
    },
    methods: {

        onMouseMove: function(e)
        {
            this.mouseX = e.pageX;
            this.mouseY = e.pageY;
        },

        onMouseDown: function(event)
        {
            /* eslint-disable no-console */
            // console.log('onMouseDown');
            /* eslint-enable no-console */

            switch (event.which)
            {
                case 1:
                    Nui.send('http://frp_creator/mouseLeftClick', JSON.stringify({
                        screenW: window.screen.availWidth,
                        screenH: window.screen.availHeight,
                
                        mouseX: this.mouseX,
                        mouseY: this.mouseY,
                    }));
                    break;
            }
        },

        onWheel: function(event)
        {
            /* eslint-disable no-console */
            // console.log('onWheel', event);
            /* eslint-enable no-console */

            if(event.deltaY !== 0)
            {
                if (event.deltaY <= 0)
                {
                    // wheeled up
                    this.postMouseWheelMovement(true);
                }
                else
                {
                    // wheeled down
                    this.postMouseWheelMovement(false);
                }
            }
        },

        postMouseWheelMovement: function(isUp)
        {
            /* eslint-disable no-console */
            // console.log('postMouseWheelMovement');
            /* eslint-enable no-console */

            Nui.send('http://frp_creator/mouseWheelMovement', JSON.stringify({
                isUp: isUp,
            }))
        },

        selectMenu: function(event) { 
            switch (event.keyCode) {
                case 39: // right
                    this.clickButtonRight();
                    break;
                case 37: // left
                    this.clickButtonLeft();
                    break;
                case 38:  // up                         
                    var menuSelectedUp = this.gMenuSelected[this.focusedMenuIndex].inputs; 

                    if (menuSelectedUp[0].name === "Nome Completo"){
                        return
                    }

                    if (this.focusedInputIndex === null) {
                        this.focusedInputIndex = 0;
                    } else if (this.focusedInputIndex > 0) {
                        this.focusedInputIndex--;
                    } 

                    if(this.focusedInputIndex >= 5) {                        
                        this.$refs.scrollDiv.scrollTop -= 55;
                    }                 
                        this.subTitleInputSelected = menuSelectedUp[this.focusedInputIndex].subTitle;      
                    break;
                        
                case 40: // down
                    var menuSelectedDown = this.gMenuSelected[this.focusedMenuIndex].inputs;

                    if (menuSelectedDown[0].name === "Nome Completo"){
                        return
                    }

                    if (this.focusedInputIndex === null) {
                        this.focusedInputIndex = 0;
                    } else if (this.focusedInputIndex < menuSelectedDown.length - 1) {
                        this.focusedInputIndex++;
                    }
                    if(this.focusedInputIndex >= 5) {                        
                        this.$refs.scrollDiv.scrollTop += 55;
                    }

                    this.subTitleInputSelected = menuSelectedDown[this.focusedInputIndex].subTitle;
                    break;       
                case 13: // enter
                    this.onPressedEnter();
                    break;
                case 8: // backspace                    
                    this.onPressedBack();
                    break;
                case 27: // esc
                    this.onPressedBack(); 
                    break;
                case 65: // KEY: A
                    this.onPressedPedRotationControl(false);
                    break;
                case 68: // KEY: D
                    this.onPressedPedRotationControl(true);
                    break;
            }   
        },

        onPressedPedRotationControl(rotateRight)
        {
            Nui.send('https://frp_creator/state:Customization:rotateCurrentPed', { rotateRight: rotateRight } );
        },
        
        onPressedEnter: function(){

            var menuSelectedEnter = this.gMenuSelected[this.focusedMenuIndex];
            var data = menuSelectedEnter.inputs[this.focusedInputIndex];        
                        
            if (!this.characterName && data.sendTo != "name") {
                return
            }

            switch(data.sendTo){
                case 'name':                        
                    Nui.send('https://frp_creator/state:Customization:requestTypeProfileName');
                break;
                case 'creatorMenu':         
                    var menuSelectedCreator = 'creatorMenu';
                    this.gMenuSelected = this[menuSelectedCreator];            
                break;
                case 'clothesMenu':         
                    var menuSelectedClothes = 'clothesMenu';
                    this.gMenuSelected = this[menuSelectedClothes];            
                break;
                case 'confirm':
                    this.ButtonDone(); 
                break;
            }

            if(!data.focusedMenuIndex){
                return
            }

            this.focusedMenuIndex = data.focusedMenuIndex;    
            this.focusedInputIndex = 0;
        },  

        onPressedBack: function(){
            var menuSelected = this.gMenuSelected[this.focusedMenuIndex];

            if(menuSelected.return >= 0){
                this.focusedMenuIndex = menuSelected.return;
                this.focusedInputIndex = 0;
                return
            }

            if(menuSelected.menuReturn){
                var menuVariation = menuSelected.menuReturn;
                this.gMenuSelected = this[menuVariation];
            }
        },          
        
        clickButtonLeft: function(){
            var menuSelected = this.gMenuSelected[this.focusedMenuIndex];
            var data = menuSelected.inputs[this.focusedInputIndex];

            var hashid = data.id;
            var facefeature = data.hashFeacture || 0;
            var step = data.steps;

            var model = 0;
            var variation = 0;

            if (data.value <= data.minValue) {
                data.value = data.maxValue + step;
            }

            // value = Number(String.split(value, '/')[0]);

            if (step != 0.1) {
                data.value -= 1
            } else {
                data.value = parseFloat(data.value) - 0.1;
                data.value = data.value.toFixed(1);

                if(data.value == 1.0) {
                    data.value = 1.01
                } else if (data.value == -1.0){
                    data.value = -1.01
                }
            }
            model = data.value;

            if (data.typeOfOverlay){                
                var typeOf = data.typeOfOverlay;

                this.updateOverlayToPed(hashid, model, variation, typeOf);
                return
            }

            this.updateComponentToPed(hashid, model, variation, facefeature);
        },

        clickButtonRight: function(){
            var menuSelected = this.gMenuSelected[this.focusedMenuIndex];
            var data = menuSelected.inputs[this.focusedInputIndex];

            var hashid = data.id;
            var facefeature = data.hashFeacture || 0;
            var step = data.steps;

            var model = 0;
            var variation = 0;

            if (data.value >= data.maxValue) {
                data.value = data.minValue - step;
            }

            // // value = Number(String.split(value, '/')[0]);

            if (step != 0.1) {
                data.value += 1
            } else {
                data.value = parseFloat(data.value) + 0.1
                data.value = data.value.toFixed(1);

                if(data.value == 1.0) {
                    data.value = 1.01
                }  else if (data.value == -1.0){
                    data.value = -1.01
                }
            }

            model = data.value;

            if (data.typeOfOverlay){                
                var typeOf = data.typeOfOverlay;

                this.updateOverlayToPed(hashid, model, variation, typeOf);
                return
            }
            
            this.updateComponentToPed(hashid, model, variation, facefeature);
        },
        updateOverlayToPed: function(compType, model, variation, typeOf) {     
            var ovelayArrayIndex = this.ComponentsSaved.Overlay[compType];

            if (!ovelayArrayIndex && compType != "heads"){
                this.ComponentsSaved.Overlay[compType] = {model:0,color:0,opacity:1.0};
            }

            switch(typeOf){
                case "model":                   
                    this.ComponentsSaved.Overlay[compType].model = model;

                    break;
                case "color":                    
                    this.ComponentsSaved.Overlay[compType].color = model;
                    break;

                case "opacity":
                    this.ComponentsSaved.Overlay[compType].opacity = model;
                    break;
            }

            if (compType != "heads"){
                Nui.send('https://frp_creator/state:Customization:OverlayType', {
                    compType: compType,
                    offsetModel:    this.ComponentsSaved.Overlay[compType].model,
                    offsetVariation: this.ComponentsSaved.Overlay[compType].color,
                    opacity: this.ComponentsSaved.Overlay[compType].opacity,
                    skinTone: this.ComponentsSaved.SkinToneVariation
                    }
                ); 
            } else {
                Nui.send('https://frp_creator/state:Customization:OverlayType', {
                    compType: compType,
                    offsetModel:    0,
                    offsetVariation: 0,
                    opacity: 0,
                    skinTone: this.ComponentsSaved.SkinToneVariation
                    }
                ); 
            }

        },
        onHoverInput: function(data){
            this.focusedInputIndex = data;
            var menuSelected = this.gMenuSelected[this.focusedMenuIndex].inputs;

            this.subTitleInputSelected = menuSelected[this.focusedInputIndex].subTitle;
        },      
        updateComponentToPed: async function(compType, model, Variation, facefeature) {
            switch(compType){
                case "heads":
                    this.ComponentsSaved.Head = model;
                    Variation = this.ComponentsSaved.SkinToneVariation;

                    this.updateOverlayToPed("heads", 0, 0);

                    break;
                case "hair":
                    this.ComponentsSaved.Hair = model;
                    Variation = this.ComponentsSaved.HairColor-1;

                    break;  
                case "teeth":
                    Variation = model;                    
                    model = 1;
                    break;
                case "mustache":
                    this.ComponentsSaved.Mushtache = model;
                    Variation = this.ComponentsSaved.MustacheColor-1;
                    
                    break;
                case "hair-color":                
                    this.ComponentsSaved.HairColor = model;           

                    compType = 'hair';
                    model = this.ComponentsSaved.Hair;
                    Variation = this.ComponentsSaved.HairColor-1;

                    break;                    
                case "mustache-color":
                    this.ComponentsSaved.MustacheColor = model;

                    compType = 'mustache';
                    model = this.ComponentsSaved.Mushtache;
                    Variation = this.ComponentsSaved.MustacheColor-1;

                    break;
                case "BODIES_UPPER":                 

                    Variation = this.ComponentsSaved.SkinToneVariation;

                    break;
                case "skin_tone":

                    this.ComponentsSaved.SkinToneVariation = model;

                    Nui.send('https://frp_creator/state:Customization:ComponentTypeSelected', {
                        compType: 'heads',
                        offsetModel: this.ComponentsSaved.Head,
                        offsetVariation: this.ComponentsSaved.SkinToneVariation,

                        faceFeatureIndex: facefeature
                    });

                    Nui.send('https://frp_creator/state:Customization:ComponentTypeSelected', {
                        compType: 'BODIES_UPPER',
                        offsetModel: this.ComponentsSaved.SkinModel,
                        offsetVariation: this.ComponentsSaved.SkinToneVariation,

                        faceFeatureIndex: facefeature
                    });                    
                    
                    this.updateOverlayToPed("heads", 0, 0);
                    break;
                case "eyes":
                        Variation = model;
                        model = 1;                    
                    break;              
            }

            Nui.send('https://frp_creator/state:Customization:ComponentTypeSelected', {
                    compType: compType,
                    offsetModel: model,
                    offsetVariation: Variation,

                    faceFeatureIndex: facefeature
                }
            );         
        },
        clearErrors: function() {
            this.errors.charName = [];
            this.errors.date = [];
        },
        showError: function(field, message) {
            switch (field) {
                case "charname":
                    this.errors.charName.push(message);
                    break;
                case "date":
                    this.errors.date.push(message);
                    break;
            }
        },

        validateStep: function() {
            this.clearErrors();
            if (!this.Menu1[0].value.match(
                    this.validation.usernameRegex
                )) {
                Nui.send('https://frp_creator/Notification.Simple',  "O nome do personagem não é válido");
                this.errors.charName.push();
                return false;
            }
            if (
                this.Menu1[0].value < 18 ||
                this.Menu1[0].value > 98
            ) {
                Nui.send('https://frp_creator/Notification.Simple',  "Idade Inválida.");
                return false;
            }
            this.menuStep = "Step2"
        },

        changeoptions: function(event) {

            if (event.target.id == "name") {
                this.Menu1[0].value = event.target.value
            }

            if (event.target.id == "age") {
                this.Menu1[1].value = event.target.value
            }
        },

        // updateinput: function(key) {
        //     var nvalue = 0;
        //     var facefeature = key.hashFeacture;

        //     if (key.value > key.minValue) {
        //         key.value = key.maxValue;
        //     }

        //     if (key.value < key.maxValue) {
        //         key.value = key.minValue;
        //     }
        //     var keyValue = key.value
        //     key.value = keyValue;

        //     nvalue = key.value;

        //     Nui.send('https://frp_creator/state:Customization:ComponentTypeSelected', {
        //         compType: null,
        //         offset: nvalue,

        //         facefeature: facefeature
        //     });
        // },


        SelectStep: function(step) {
            if (this.menuStep === 'Step1') {
                if (this.validateStep() && this.Clicked == 0) {
                    this.Clicked = 1;
                    //var charAge = this.Menu1[1].value
                }
            }
            this.menuStep = step
        },

        ButtonClose: function() {
            Nui.send('https://frp_creator/CharacterCreation.Cancel');
        },

        ButtonDone: function() {     
            var mainMenu = this.gMenuSelected[this.focusedMenuIndex];
            var ageValue = mainMenu.inputs[1];

            Nui.send('https://frp_creator/CharacterCreation.Done', {
                age: ageValue,
                overlayArray: this.ComponentsSaved.Overlay
            });
        },
    },
    beforeDestroy() {
        window.removeEventListener('keydown', this.selectMenu);
    },
};
</script>

<style lang="scss">

/* @import 'https://cdn.jsdelivr.net/npm/bootstrap@5.0.0/dist/css/bootstrap.min.css'; */

@font-face {
    font-family: "crock";
    src: url("https://luminousrp.com/fonts/typography/crock.ttf") format("truetype");
}
@font-face {
    font-family: "Typewriter";
    src: url("https://luminousrp.com/fonts/typography/Typewriter.ttf") format("truetype");
}
@font-face {
    font-family: "Redemption";
    src: url("https://luminousrp.com/fonts/typography/Redemption.ttf") format("truetype");
}
@font-face {
    font-family: "Hapna";
    src: url("https://luminousrp.com/fonts/typography/Hapna.ttf") format("truetype");
}

.view {
    user-select: none;
    text-overflow: ellipsis;
    position: absolute;
    margin: 0;
	overflow: hidden;
    height: 98%;
    width: 100%;
}

body{
    height:0px;
    overflow-x: hidden; //horizontal 
    overflow-y: scroll; //vertical
    background:transparent;
    

}

*{

	text-overflow: ellipsis;

    ::-webkit-scrollbar {
        display: none;
    }
    
    :focus{outline: none;}
}


.disabled {
    opacity: 0.4;
    user-select: none;
}
.view--char_creation {
    #char_creation {
         transition: all 0.5s !important;
        // background-image: url('https://seanmills6.files.wordpress.com/2019/07/rdro-cameron-04.jpg');
        // background-size: 100% 100%;
        // background-repeat: no-repeat;
        margin: 0;
        width: 100%;        
        display:flex;
        align-items: center;
        text-align: center;
        height: 100%; 
            overflow-x: hidden; //horizontal 
    overflow-y: scroll; //vertical
        .char_creation_container {      
            position: absolute;
            width: 38.5vh;  
            height: 65vh;
            padding-bottom: 3vh;
            max-height: 85vh;
            max-width: 38.5vh;
            left:50px;
            display:flex;
            justify-content: center;
            align-items: center;
            text-align: center;
            color:rgba(254,254,254,0.8);
            flex-direction: column ;
            
            background-size: 100% 100%;
            background-image: url('https://i.ibb.co/PrffsBm/bgPanel.png');
            background-repeat: no-repeat;
            background-position: center bottom; 

            .line-divider-horizontal{
                position: absolute;
                left: 0;    
                bottom: 3.2vh;
                width: 100%;
                padding-top: 1vh;
                background-size: 80% 2px;
                background-image: url('https://i.ibb.co/sgDGnQQ/divider-line.png');
                background-repeat: no-repeat;
                background-position: center top;
                
            }
            .char_creation_header{           
                width: 100%;
                position: relative;     
                background-size: 100% 100%;
                margin: 10px 0 0 0px;
                padding:15px 10px 0 10px;
                color:rgba(255,255,255,1);
                text-align: center;
                justify-content: space-between;
                line-height: 1em;
                font-family: 'crock';
                flex-direction:column;
                display: flex;
                align-items: center;

                .title{
                    padding-top:0vh;           
                    width:92%;         
                    height:8vh;
                    display: flex;
                    justify-content:center;
                    align-items: center;
                    background-image: url('https://i.ibb.co/cCgCx5F/menu-header-1a.png');
                    background-repeat: no-repeat;
                    background-position: center; 
                    -webkit-background-size: contain; 
                    background-size: contain;    
                    font-size:3.7vh;
                    margin-bottom:8px; 
                }
                .subTitle{                    
                    font-size:0.4em;
                }
             //   border-bottom : dotted 0.05em rgba(0,0,0,0.8);
                margin-bottom: 0px;
            //     background-size: 95% 4px;
            //    // background-image: url('https://i.ibb.co/sgDGnQQ/divider-line.png');
            //     background-repeat: no-repeat;
            //     background-position: center bottom;
                
                //span{     font-family: 'crock'; text-transform: uppercase;}
        
            }
            .char_creation_content{
                width: 100%;    
                height: 90%;
                overflow: hidden;                
                margin-bottom: 20px;
                margin: 0px 5px 0 5px; 
                
                // .menuOption{
                //     display:flex;
                //     flex-direction: column;
                //     width: 100%;
                //     .menuButtons{
                //         font-size:1.0em;
                //         background: transparent;
                //         border: none;
                //         background-size: 100% 100%;
                //         padding: 10px 10px;                 
                //         width: 256px;
                //         background-image: url(@\\assets\\bgButton.png);
                //         margin-right: 20px;                        
                //         color: rgba(33,32,31,1);
                //         &:hover {
                //             color: rgba(0,0,9,0.8) !important;
                //         }
                //     }
                // }
                .body_char_creation{
                    position: relative;                    
                    padding: 10px 20px 10px 20px;
                  //  width:85%;
                    font-weight: 500;
                    text-align: start;
                    max-height: 45vh;                    
                    overflow-y: scroll;
                    margin-bottom:10px;
                        // background-size: 85% 2px;
                        // background-image: url('https://i.ibb.co/sgDGnQQ/divider-line.png');
                        // background-repeat: no-repeat;
                        // background-position: center bottom;  
                    .input-number, .input-text{
                        height:auto;
                        color: rgba(255,255,255,0.6);                               
                        font-size:0.95em; 
                        font-family: "Hapna" !important;
                    }
                    .input-enter{
                        margin: 0px 24px 0 35px;
                        background-image: url('https://i.ibb.co/NCQ1HQd/input-enter.png');
                        background-repeat: no-repeat;
                        background-size: 100% 100%;
                        background-position: center;
                         opacity: .6
                    }
                    .input{
                        
                        input{
                            font-size:0.9em; 
                            font-family: "Hapna" !important;
                            background: transparent;
                            border: none;          
                            width: auto;
                            min-width: 280px;
                            height:30px;
                            user-select: none;
                        }                               
                    }          
                    input[type=number]::-webkit-inner-spin-button, 
                    input[type=number]::-webkit-outer-spin-button { 
                        color:white;
                      -webkit-appearance: none; 
                      margin: 0; 
